.markdown-body {
  box-sizing: border-box;
  min-width: 200px;
  max-width: 980px;
  margin: 0 auto;
  padding: 45px;
  color: #314659;
  font-weight: 400; }
  .markdown-body h1,
  .markdown-body h2 {
    border: none; }

.markdown-body:first-child:not(:last-child) {
  padding-bottom: 0; }

.markdown-body:not(:last-child) {
  margin-bottom: 24px; }

.markdown-body:not(:first-child) {
  padding-top: 0; }
